@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
:root{--bg-color: #003049;--accent-1: #EAE2b7;--accent-2: #f77f00}

*{margin:0;padding:0}body{font-family:'Montserrat', sans-serif;font-weight:normal;background-color:var(--bg-color)}.App{height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center}

.Header{margin-top:2rem;padding-bottom:2rem;display:flex;flex-direction:column;align-items:center;grid-row-gap:1.5rem;row-gap:1.5rem}.Header h1{font-weight:bold;text-align:center;color:var(--accent-1)}@media only screen and (min-width: 1024px){.Header{width:100%;padding-top:2rem;flex-direction:row;justify-content:space-around;align-items:center}.Header h1{font-size:3rem}}

.GithubHeader{display:flex;flex-direction:column;align-items:center;text-align:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.GithubHeader a{text-decoration:none}.GithubHeader .github-logo{width:2em;height:2em;display:inline-block;fill:#d62828}.GithubHeader h2{color:white;font-weight:normal}@media only screen and (min-width: 1024px){.GithubHeader .github-logo{width:3em;height:3em}}

.ScoreBoard{width:90vw;height:7vh;background-color:#024264;border-radius:2rem;display:flex;font-family:'Montserrat', sans-serif;font-weight:400;color:var(--accent-1);font-size:16px;box-shadow:0 5px 10px rgba(0,0,0,0.3)}.ScoreBoard div{width:50%;height:100%;display:flex;flex-direction:column;justify-content:center;text-align:center}.ScoreBoard .current-score{border-right:1px solid var(--accent-1)}.ScoreBoard .best-score{border-left:1px solid var(--accent-1)}@media only screen and (min-width: 1024px){.ScoreBoard{width:40vh}}

.CardContainer{width:100vw;height:100%;padding:2rem;box-sizing:border-box;overflow-y:scroll}.CardContainer .cards{width:100%;height:100%;display:grid;grid-template-columns:50% 50%;grid-column-gap:1rem;-webkit-column-gap:1rem;column-gap:1rem;grid-row-gap:2rem;row-gap:2rem;justify-content:space-around}.CardContainer .cards::after{content:"";display:block;height:1rem;width:100%}@media only screen and (min-width: 1024px){.CardContainer{display:flex;height:100%;overflow:hidden;justify-content:center;align-items:center}.CardContainer .cards{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;grid-template-columns:16% 16% 16% 16% 16% 16%;grid-row-gap:8rem;row-gap:8rem;grid-auto-rows:minmax(-webkit-min-content, -webkit-max-content);grid-auto-rows:minmax(min-content, max-content)}.CardContainer .cards::after{display:none}}

.Card{width:90%;height:126px;background-color:#5ca8cf;border-radius:1rem;justify-self:center;display:flex;flex-direction:column;justify-content:flex-end;align-items:center;grid-gap:1rem;gap:1rem;box-shadow:0 10px 10px rgba(0,0,0,0.3);color:white;font-weight:bold;letter-spacing:1px}.Card:hover{transform:scale(1.1);transition:ease-in-out 0.2s}.Card img{width:4rem;height:4rem;color:white}.Card p{padding-bottom:0.5rem}@media only screen and (min-width: 1024px){.Card{height:200px;justify-content:center;grid-gap:2rem;gap:2rem;font-size:1.5rem;letter-spacing:2px}}

